.footer{
    margin: 50px 170px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.footer-top{
    display: flex;
    justify-content: space-between;
}
.footer-top-left img{
    width: 150px;
}
.footer-top-left p{
font-size: 20px;
max-width: 400px;
}
.footer-top-right{
    display: flex;
    align-items: center;
    gap: 35px;
}
.footer-email-input{
    display: flex;
    gap: 30px;
    padding: 20px 30px;
    border-radius: 50px;
    /* background: #32323B; */
    padding-right: 80px;
}

.footer-email-input input{
    outline: none;
    border: none;
    background: transparent;
    color:#a0a0a0 ;
    font-size: 18px;
}
.footer-subscribe{
    font-size: 20px;
    padding: 22px 50px;
    border-radius: 50px;
    background: linear-gradient(267deg, #DA7C25 0.36%,#B923E1 102.06%);
    cursor: pointer;
    transition: 0.3s;
}
.footer-bottom{
    display: flex;
    justify-content: space-between;
    font-size: 18px;
}
.footer-bottom-right{
    display: flex;
    gap: 50px;
    margin-bottom: 50px;
}
.footer-email-input:hover{
    border: 2px solid white;
}
.footer-subscribe:hover{
    transform: scale(1.1);
    transition: 0.3s;
}
@media(max-width:768px){
    .footer{
        margin: 50px 70px;
    }
    .footer-top{
        flex-direction: column;
    }
    .footer-bottom{
        display: flex;
        flex-direction: column-reverse;
        margin-bottom:50px ;

    }
}

@media(max-width:480px){
    .footer{
        margin: 50px 20px;
    }
    .footer-top-left p{
        font-size: 16px;
        max-width: 100%;
    }
    .footer-email-input{
        padding: 15px 20px;
        padding-right: 60px;
    }
    .footer-email-input input{
        font-size: 14px;
    }
    .footer-subscribe{
        font-size: 18px;
        padding: 12px 30px;
    }
    .footer-bottom{
        font-size: 16px;
    }
    .footer-bottom-right{
        gap: 30px;
        margin-bottom: 30px;
    }
    .footer-top-right{
        flex-direction: column;
    }
    .footer-top-left img{
        width: 70px;
    }
}
