.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 170px;
    /* position: sticky;
    top: 0;
    z-index: 1000; */
}
.navbar img{
    width: 150px;
    height: 120px;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 60px;
    font-size: 20px;
    cursor: pointer;
}
.nav-connect{
    padding: 20px 40px;
    border-radius: 50px;
    font-size: 22px;
    background:linear-gradient(267deg, #DA7C25 0.36%,#B923E1 102.06%) ;
    cursor: pointer;
    transition: 0.5s;
}
.nav-connect:hover{
    transform: scale(1.05)
}
.themechange{
    display: flex;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
}
.anchor-link{
    text-decoration: none;
    color: white;
    /* color: #1f0016; */
    font-size: 50px;
    font-weight: 700;
}

.nav-mob-close{
    display: none;
    color: #1f0016;
}
.nav-mob-open{
    display: none;
    height: 50px;
}
@media(max-width:768px){
    .navbar{
        margin: 20px 50px;
    }
    .nav-connect{
        display: none;
    }
    .nav-mob-open{
        display: block;
        position: fixed;
        right: 30px;
        max-width: 50px;
    }
    .nav-mob-close{
        display: block;
        position: relative;
        top: 30px;
        left: 230px;
       max-width: 50px;

    }
    .nav-menu{
        position: fixed;
        flex-direction: column;
        align-items:start;
        top: 0;
        gap: 30px;
        /* background-color:#1f0016; */
        width: 350px; 
        height: 100px;
        z-index: 2;
        transition: right 0.5s;
        right: -350px;
    }
    .nav-menu li{
        font-size: 30px;
        padding-left:100px ;
        flex-direction: row;
        gap: 20px;
    }
}


@media(max-width:480px){
    .navbar{
        margin: 20px 20px;
    }
    .navbar img{
        width: 90px;
        height: 70px;
    }
    .nav-menu{
        gap: 20px;
        width: 280px;
        height: auto;
    }
    .nav-menu li{
        font-size: 24px;
        padding-left: 20px;
    }
    .nav-mob-open{
        right: 20px;
        
    }
    .nav-mob-close{
        left: 200px;
    }
}
