.hero{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 35px;
}
.hero img{
    margin-top: 150px;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    align-self: center;
}
.hero h1{
    text-align: center;
    width: 70%;
    font-size: 90px;
    font-weight: 600;
}
.hero h1 span{
    background:linear-gradient(270deg,#DF8908 10% , #B415FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent ;
}
.hero p{
    width: 50%;
    text-align: center;
    font-size: 24px;
    line-height: 40px;
}
.hero-action{
    display: flex;
    align-items: center;
    gap: 25px;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 50px;
}
.hero-connect{
   padding: 20px 45px;
   border-radius: 50px;
   background: linear-gradient(264deg,#DF8908 -5.09%, #B415FF 106.5%);
   cursor: pointer; 
}
.anchor-link{
    font-size: 18px;
}
.hero-resume{
    padding: 20px 75px;
    border-radius: 50px;
    border: 2px solid white;
    cursor: pointer;
    outline: none;
    color:white;
    position: relative;
}
.hero-connect:hover{
    border: 2px solid #fff;
}

.hero-resume:hover{
    border: 2px solid #B415FF;
}

@media(max-width:768px){
    .hero img{
      margin-bottom: 60px;
      width: 220px;  
      align-self: center;
    }
    .hero h1{
        margin-top: 50px;
        width: 90%;
        font-size: 50px;
    } 
    .hero p{
        width: 60%;
        font-size: 18px;
        line-height: 32px;
        margin: 10px 0px;
    }
    .hero-action{
        gap: 16px;
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 50px;
    }
    .hero-connect{
        padding: 20px 30px;
        border-radius: 40px;
    }
    .hero-resume{
        padding: 20px 50px;
        border-radius: 40px;
    }
}

@media(max-width:480px){
    .hero img{
        margin-bottom: 40px;
        width: 180px;
    }
    .hero h1{
        margin-top: 40px;
        width: 90%;
        font-size: 40px;
    } 
    .hero p{
        width: 80%;
        font-size: 16px;
        line-height: 28px;
        margin: 10px 0px;
    }
    .hero-action{
        gap: 12px;
        font-size: 16px;
        margin-bottom: 40px;
    }
    .hero-connect{
        padding: 16px 10px;
        border-radius: 30px;
    }
    .hero-resume{
        padding: 16px 36px;
        border-radius: 30px;
    }
}
